/* Analytics.css */
.card {
    margin-bottom: 20px;
  }
  
  .card-header {
    background-color: #f7f7f7; /* Background color for card headers */
    border-bottom: 1px solid #ddd; /* Add a subtle border */
    font-size: 18px;
  }
  
  .card-body {
    padding: 20px;
  }
  
  .text-success {
    color: #28a745; /* Text color for success messages */
  }
  
  .text-danger {
    color: #dc3545; /* Text color for danger messages */
  }
  
  .progress-circle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }
  
  /* Customize Progress bar styles */
  .ant-progress-inner {
    border-radius: 50%;
    background-color: #f0f0f0; /* Background color for progress bars */
  }
  
  /* Customize Progress circle text color */
  .ant-progress-text {
    color: #333;
  }
  
  /* Category cards */
  .category-card {
    margin-bottom: 20px;
    background-color: #f7f7f7;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 10px;
  }
  
  .category-card h5 {
    margin-bottom: 10px;
    font-size: 16px;
  }
  
