/* HomePage.css */
.filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.filters h6 {
  margin-bottom: 8px;
  font-size: 16px;
}

.icon-switch {
  display: flex;
  align-items: center;
}

.icon-switch .active-icon {
  color: #007bff; /* Change this to your desired active icon color */
  font-size: 24px; /* Adjust the font size as needed */
  cursor: pointer;
}

.icon-switch .inactive-icon {
  color: #ccc; /* Change this to your desired inactive icon color */
  font-size: 24px; /* Adjust the font size as needed */
  cursor: pointer;
}

.content {
  background-color: #f8f8f8; /* Change this to your desired background color */
  padding: 20px;
  border-radius: 5px;
}

.modal-content {
  background-color: #fff; /* Change this to your desired modal content background color */
  border-radius: 5px;
}

.modal-header {
  background-color: #007bff; /* Change this to your desired modal header background color */
  color: #fff; /* Text color for the modal header */
}

.modal-title {
  font-size: 20px; /* Adjust the font size as needed for the modal title */
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
}

.modal-footer button {
  margin-left: 10px;
}

/* Add more styles as needed to match your design */
