 .register-page {
 
    height: 100vh !important;
    width: 100vw !important;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(240, 207, 21, 0.35) 0px 5px 15px;
  }
  
  .register-page {
    /* background-color: #161617b6; */
    margin-left: -15px;
    padding: 30px;
  }
  
  .register-page h2 {
    text-align: center;
    font-weight: bold;
    margin: 30px;
    color: #344265;
  }
  
  .register-page a {
    color: #344265 !important;
  }
  
  .register-page a:hover {
    color: black !important;
  }
  
  .register-page button {
   
      background-color: #344265;
      color: white;
      outline: none;
    
  }
  
  .register-page input:focus {
    border: 1px solid #b87333 !important;
    color: #b87333;
  }
  .register-page button:hover {
    background-color: black;
    color: white;
  } 